    .bs-callout {
        background: white;
        border-color: #eee;
        -webkit-border-image: none;
                border-image: none;
        border-radius: 3px;
        border-style: solid;
        border-width: 1px 1px 1px 5px;
        margin-bottom: 5px;
        padding: 20px;
    }
    .bs-callout:last-child {
        margin-bottom: 0px;
    }
    .bs-callout h4 {
        margin-bottom: 10px;
        margin-top: 0;
    }
    
    .bs-callout-danger {
        border-left-color: green;
    }
    
    .bs-callout-danger h4{
        color: green;
    }
    
    
    
    
        

